<template>
  <div class="root">
  <div class="hang_up">
      <div class="hang flex">
        <div class="des one-line">设置加人性别</div>
        <div class="des_sex_none one-line" v-if="sexSel != '不限'" @click="sexnjs()">不限</div>
        <div class="des_sex_none_sel one-line"  v-if="sexSel == '不限'">不限</div>
        <div class="des_sex_man one-line" v-if="sexSel != '男性'" @click="sexmjs()">男性</div>
        <div class="des_sex_man_sel one-line" v-if="sexSel == '男性'">男性</div>
        <div class="des_sex_woman one-line" v-if="sexSel != '女性'" @click="sexwjs()">女性</div>
        <div class="des_sex_woman_sel one-line" v-if="sexSel == '女性'">女性</div>
      </div>
      <div class="margin-line"></div>
      <div class="hang flex">
      <div class="des one-line">设置加人距离</div>
      <div class="des_sex_none one-line" v-if="myPlace != '第1个'" @click="setpnjs()">不限</div>
      <div class="des_sex_none_sel one-line"  v-if="myPlace == '第1个'">不限</div>
      <div class="des_sex_man one-line" v-if="myPlace != '第10个'" @click="setpmjs()">&lt;10km</div>
      <div class="des_sex_man_sel one-line" v-if="myPlace == '第10个'">&lt;10km</div>
      <div class="des_sex_woman one-line" v-if="myPlace != '第100个'" @click="setpwjs()">&gt;10km</div>
      <div class="des_sex_woman_sel one-line" v-if="myPlace == '第100个'">&gt;10km</div>
    </div>
      <div class="margin-line"></div>
    <!--<div class="bottom-line"></div>-->
  </div>
   <div class="hang_bottom flex">
     <div class="bottom_hang flex" v-if="needCoin != '0金币'">
       <div class="des1 one-line">{{myCoinDes}}</div>
       <div class="des2 one-line">{{needCoin}}</div>
       <div class="des3 one-line">{{myCoin}}</div>
     </div>
     <div class="bottom_btn" @click="gafjs()">{{btnText}}</div>
   </div>
  </div>
</template>

<script>
export default{
  name: 'NearByAddFriend',
  data () {
    return {
      sexSel: '',
      switchSel: '',
      btnText: '',
      needCoin: '',
      myCoin: '',
      myMode: '',
      myPlace: '',
      myGreet: '请设置打招呼语',
      myCoinDes: ''
    }
  },
  mounted () {
    window.setSexSel = this.setSexSel
    window.setSwitchSel = this.setSwitchSel
    window.setBtnText = this.setBtnText
    window.setNeedcoin = this.setNeedcoin
    window.setMycoin = this.setMycoin
    window.setMymode = this.setMymode
    window.setMyplace = this.setMyplace
    window.setGreet = this.setGreet
    window.setMycoindes = this.setMycoindes
  },
  methods: {
    setSexSel (sel) {
      this.sexSel = sel
      console.log('type')
      console.log(this.sexSel)
      return sel
    },
    setSwitchSel (sel) {
      this.switchSel = sel
      return sel
    },
    setGreet (sel) {
      this.myGreet = sel
      return sel
    },
    setBtnText (txt) {
      this.btnText = txt
      return txt
    },
    setNeedcoin (txt) {
      this.needCoin = txt
      return txt
    },
    setMycoin (txt) {
      this.myCoin = txt
      return txt
    },
    setMycoindes (txt) {
      this.myCoinDes = txt
      return txt
    },
    setMymode (txt) {
      this.myMode = txt
      return txt
    },
    setMyplace (txt) {
      this.myPlace = txt
      return txt
    },
    gafjs () {
      if (window.bridge) {
        window.bridge.gaf()
      }
    },
    sexnjs () {
      if (window.bridge) {
        window.bridge.sexn()
      }
    },
    sexmjs () {
      if (window.bridge) {
        window.bridge.sexm()
      }
    },
    sexwjs () {
      if (window.bridge) {
        window.bridge.sexw()
      }
    },
    setpjs () {
      if (window.bridge) {
        window.bridge.setp()
      }
    },
    setpnjs () {
      if (window.bridge) {
        window.bridge.sexpn()
      }
    },
    setpmjs () {
      if (window.bridge) {
        window.bridge.sexpm()
      }
    },
    setpwjs () {
      if (window.bridge) {
        window.bridge.sexpw()
      }
    },
    setgjs () {
      if (window.bridge) {
        window.bridge.setg()
      }
    },
    switchonjs () {
      if (window.bridge) {
        window.bridge.switchon()
      }
    },
    switchoffjs () {
      if (window.bridge) {
        window.bridge.switchoff()
      }
    },
    setmjs () {
      if (window.bridge) {
        window.bridge.setm()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .root{
    background-color: white;
    line-height: 1.5;
    position: relative;
  }
  .hang_up{
    position: relative;
    background-color: white;
    line-height: 1.5;
    margin-bottom: 100px;
  }
  .hang{
    position: relative;
    height: 55px;
  }
  .bottom_hang{
    top: 9px;
    position: relative;
    width: 100%;
    height: 17px;
  }
  .bottom_btn{
    bottom: 12px;
    position: absolute;
    left: 5%;
    width: 90%;
    height: 34px;
    border-radius: 4px;
    background-color: #ff9f40;
    color: white;
    font-size: 17px;
    padding-top: 10px;
    text-align: center;
  }
  .bottom_hang .des1{
    position: relative;
    font-size: 12px;
    color: #2f3031;
    height: 17px;
    left: 16px;
  }
  .bottom_hang .des2{
    position: relative;
    font-size: 12px;
    left: 16px;
    color: #f97044;
    height: 17px;
  }
  .bottom_hang .des3{
    position: absolute;
    font-size: 12px;
    right: 16px;
    color: #2f3031;
    height: 17px;
  }
  .hang_bottom{
    position: fixed;
    height: 90px;
    width: 100%;
    bottom: 0px;
  }
  .hang .des{
    position: absolute;
    font-size: 16px;
    color: #2f3031;
    top:0;
    left:16px;
    bottom:0;
    margin:auto;
    height: 25px;
  }
  .hang .des_sex_none_sel{
    position: absolute;
    font-size: 14px;
    color: #ff9f40;
    text-align: center;
    padding-top: 5px;
    top:0;
    right:154px;
    bottom:0;
    margin:auto;
    width: 64px;
    height: 25px;
    opacity: 0.6;
    border-radius: 15px;
    border: solid 1px #ff9f40;
  }
  .hang .des_sex_none{
    position: absolute;
    font-size: 14px;
    color: #1c162e;
    text-align: center;
    padding-top: 5px;
    top:0;
    right:154px;
    bottom:0;
    margin:auto;
    width: 64px;
    height: 25px;
    opacity: 0.6;
    border-radius: 15px;
    border: solid 1px #d3d3d7;
  }
  .hang .des_sex_man{
    position: absolute;
    font-size: 14px;
    color: #1c162e;
    text-align: center;
    padding-top: 5px;
    top:0;
    right:85px;
    bottom:0;
    margin:auto;
    width: 64px;
    height: 25px;
    opacity: 0.6;
    border-radius: 15px;
    border: solid 1px #d3d3d7;
  }
  .hang .des_sex_man_sel{
    position: absolute;
    font-size: 14px;
    color: #ff9f40;
    text-align: center;
    padding-top: 5px;
    top:0;
    right:85px;
    bottom:0;
    margin:auto;
    width: 64px;
    height: 25px;
    opacity: 0.6;
    border-radius: 15px;
    border: solid 1px #ff9f40;
  }
  .hang .des_sex_woman_sel{
    position: absolute;
    font-size: 14px;
    color: #ff9f40;
    text-align: center;
    padding-top: 5px;
    top:0;
    right:16px;
    bottom:0;
    margin:auto;
    width: 64px;
    height: 25px;
    opacity: 0.6;
    border-radius: 15px;
    border: solid 1px #ff9f40;
  }
  .hang .des_sex_woman{
    position: absolute;
    font-size: 14px;
    color: #1c162e;
    text-align: center;
    padding-top: 5px;
    top:0;
    right:16px;
    bottom:0;
    margin:auto;
    width: 64px;
    height: 25px;
    opacity: 0.6;
    border-radius: 15px;
    border: solid 1px #d3d3d7;
  }
  .hang .sel{
    position: absolute;
    top:0;
    right:36px;
    bottom:0;
    margin:auto;
    font-size: 14px;
    color: #ff9f40;
    height: 20px;
  }
  .hang .tips{
    position: absolute;
    top:0;
    right:36px;
    bottom:0;
    margin:auto;
    font-size: 14px;
    color: #92929b;
    height: 20px;
  }
  .hang .img-link{
    position: absolute;
    top:0;
    right:16px;
    bottom:0;
    margin:auto;
    width: 20px;
    height: 20px
  }
  .hang .switch{
    position: absolute;
    top:0;
    right:16px;
    bottom:0;
    margin:auto;
    width: 42px;
    height: 24px
  }
  .root .margin-line{
    width: 90%;
    height: 1px;
    margin-left: 5%;
    background-color : #ebebf1;
  }
</style>



