<template>
  <div class="root">
  <div class="big_bg1" @click="safjs()"></div>
    <div class="wdm-add" @click="wdmjs()" v-if = "addwdm.length == 1">
      <div class="margin-line"></div>
    </div>
    <div class="big_bg2" @click="safjs()"></div>
    <div class="margin-line"  v-if = "addrrxc.length == 1"></div>
    <div class="rrxc-add" @click="rrxcjs()" v-if = "addrrxc.length == 1">
    </div>
    <div class="bottom-line"></div>
  </div>
</template>

<script>
export default {
  name: 'UnvipMember',
  data () {
    return {
      addwdm: '0',
      addrrxc: '0'
    }
  },
  mounted () {
    window.setWdm = this.setWdm
    window.setRrxc = this.setRrxc
  },
  methods: {
    setWdm (show) {
      this.addwdm = show
      return show
    },
    setRrxc (show) {
      this.addrrxc = show
      return show
    },
    wdmjs () {
      if (window.bridge) { window.bridge.wdm() }
    },
    rrxcjs () {
      if (window.bridge) { window.bridge.rrxc() }
    },
    safjs () {
      if (window.bridge) { window.bridge.saf() }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .root{
    background-color: white;
    line-height: 1.5;
    position: relative;
  }
  .big_bg1{
    height: 1130px;
    background-image: url("./../assets/big_bg1.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    -moz-background-size:100% 100%;
    position: relative;
  }
  .big_bg2{
       height: 380px;
       background-image: url("./../assets/big_bg2.png");
       background-repeat: no-repeat;
       background-size: 100% 100%;
       -moz-background-size:100% 100%;
       position: relative;
     }
  .wdm-add{
    height: 259px;
    background-image: url("./../assets/wecat_add.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    -moz-background-size:100% 100%;
    position: relative;
  }
  .rrxc-add{
    height: 400px;
    background-image: url("./../assets/rrxc_add.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    -moz-background-size:100% 100%;
    position: relative;
  }  .bottom-line{
       position: relative;
       width: 100%;
       height: 50px;
       background-color : #ffffff;
     }
  .margin-line{
    position: relative;
    width: 100%;
    height: 10px;
    background-color : #f7f7f7;
  }
</style>



