<template>
  <div class="root">
  <div class="hang_up">
    <div class="hang flex">
      <div class="des one-line">设置加人性别</div>
      <div class="des_sex_none one-line" v-if="sexSel != '不限'" @click="sexnjs()">不限</div>
      <div class="des_sex_none_sel one-line"  v-if="sexSel == '不限'">不限</div>
      <div class="des_sex_man one-line" v-if="sexSel != '男性'" @click="sexmjs()">男性</div>
      <div class="des_sex_man_sel one-line" v-if="sexSel == '男性'">男性</div>
      <div class="des_sex_woman one-line" v-if="sexSel != '女性'" @click="sexwjs()">女性</div>
      <div class="des_sex_woman_sel one-line" v-if="sexSel == '女性'">女性</div>
    </div>
    <div class="margin-line"></div>
      <div class="hang flex" @click="setaarjs()">
        <div class="des one-line">选择人脉属性</div>
        <div class="tips one-line">{{myArr}}</div>
        <div class="imgicon">
          <img src="../assets/ic_right_arow.png" class="img-link"/>
        </div>
      </div>
    <div class="margin-line"></div>
    <div class="hang flex" @click="setindjs()">
      <div class="des one-line">选择人脉行业</div>
      <div class="tips one-line">{{mInd}}</div>
      <div class="imgicon">
        <img src="../assets/ic_right_arow.png" class="img-link"/>
      </div>
    </div>
      <div class="margin-line"></div>
    <div class="hang flex" @click="setareajs()">
      <div class="des one-line">选择地区省份</div>
      <div class="tips one-line">{{myArea}}</div>
      <div class="imgicon">
        <img src="../assets/ic_right_arow.png" class="img-link"/>
      </div>
    </div>
    <div class="margin-line"></div>
    <div class="hang flex" @click="setgjs()">
      <div class="des one-line">设置打招呼语</div>
      <div class="tips one-line">{{myGreet}}</div>
      <div class="imgicon">
        <img src="../assets/ic_right_arow.png" class="img-link"/>
      </div>
    </div>
    <div class="margin-line"></div>
    <!--<div class="bottom-line"></div>-->
  </div>
   <div class="hang_bottom flex">
     <div class="bottom_hang flex" v-if="needCoin != '0金币'">
       <div class="des1 one-line">{{myCoinDes}}</div>
       <div class="des2 one-line">{{needCoin}}</div>
       <div class="des3 one-line">{{myCoin}}</div>
     </div>
     <div class="bottom_btn" @click="gafjs()">{{btnText}}</div>
   </div>
  </div>
</template>

<script>
export default{
  name: 'QrFriend',
  data () {
    return {
      sexSel: '',
      switchSel: '',
      btnText: '',
      groupSel: 'a',
      needCoin: '',
      myCoinDes: '',
      myCoin: '',
      myMode: '',
      myShield: '群发所有好友',
      myPlace: '',
      myArea: '全部省市',
      myGreet: '请设置打招呼语',
      mInd: '全部行业',
      myArr: '全部属性'
    }
  },
  mounted () {
    window.setSexSel = this.setSexSel
    window.setSwitchSel = this.setSwitchSel
    window.setBtnText = this.setBtnText
    window.setNeedcoin = this.setNeedcoin
    window.setMycoin = this.setMycoin
    window.setMycoindes = this.setMycoindes
    window.setMymode = this.setMymode
    window.setMyplace = this.setMyplace
    window.setGreet = this.setGreet
    window.setShieldSel = this.setShieldSel
    window.setAreaSel = this.setAreaSel
    window.setGroupSel = this.setGroupSel
    window.setArrSel = this.setArrSel
    window.setIndSel = this.setIndSel
  },
  methods: {
    setSexSel (sel) {
      this.sexSel = sel
      console.log('type')
      console.log(this.sexSel)
      return sel
    },
    setSwitchSel (sel) {
      this.switchSel = sel
      return sel
    },
    setGroupSel (sel) {
      this.groupSel = sel
      return sel
    },
    setAreaSel (sel) {
      this.myArea = sel
      return sel
    },
    setArrSel (sel) {
      this.myArr = sel
      return sel
    },
    setIndSel (sel) {
      this.mInd = sel
      return sel
    },
    setShieldSel (sel) {
      this.myShield = sel
      return sel
    },
    setGreet (sel) {
      this.myGreet = sel
      return sel
    },
    setBtnText (txt) {
      this.btnText = txt
      return txt
    },
    setNeedcoin (txt) {
      this.needCoin = txt
      return txt
    },
    setMycoin (txt) {
      this.myCoin = txt
      return txt
    },
    setMycoindes (txt) {
      this.myCoinDes = txt
      return txt
    },
    setMymode (txt) {
      this.myMode = txt
      return txt
    },
    setMyplace (txt) {
      this.myPlace = txt
      return txt
    },
    gafjs () {
      if (window.bridge) {
        window.bridge.gaf()
      }
    },
    sexnjs () {
      if (window.bridge) {
        window.bridge.sexn()
      }
    },
    sexmjs () {
      if (window.bridge) {
        window.bridge.sexm()
      }
    },
    sexwjs () {
      if (window.bridge) {
        window.bridge.sexw()
      }
    },
    setpjs () {
      if (window.bridge) {
        window.bridge.setp()
      }
    },
    setgjs () {
      if (window.bridge) {
        window.bridge.setg()
      }
    },
    setareajs () {
      if (window.bridge) {
        window.bridge.setarea()
      }
    },
    setaarjs () {
      if (window.bridge) {
        window.bridge.setaar()
      }
    },
    setindjs () {
      if (window.bridge) {
        window.bridge.setind()
      }
    },
    setlabeljs () {
      if (window.bridge) {
        window.bridge.setlabel()
      }
    },
    switchonjs () {
      if (window.bridge) {
        window.bridge.switchon()
      }
    },
    switchoffjs () {
      if (window.bridge) {
        window.bridge.switchoff()
      }
    },
    setmjs () {
      if (window.bridge) {
        window.bridge.setm()
      }
    }
  }
}
</script>

