    color: #ff9f40;
    height: 20px;
  }
  .hang .tips{
    position: absolute;
    top:0;
    right:36px;
    bottom:0;
    margin:auto;
    font-size: 14px;
    color: #92929b;
    height: 20px;
  }
  .hang .img-link{
    position: absolute;
    top:0;
    right:16px;
    bottom:0;
    margin:auto;
    width: 20px;
    height: 20px
  }
  .hang .switch{
    position: absolute;
    top:0;
