<template>
  <div class="root">
  <div class="hang_up">
    <div class="hang flex" @click="setmjs()">
      <div class="des one-line">设置模式</div>
      <div class="sel one-line">{{myMode}}</div>
      <div class="imgicon">
        <img src="../assets/ic_right_arow.png" class="img-link"/>
      </div>
    </div>
    <div class="margin-line"></div>
    <div class="hang flex" @click="setselnumjs()">
        <div class="des one-line">设置数量</div>
        <div class="tips one-line">{{selNum}}</div>
        <div class="imgicon">
          <img src="../assets/ic_right_arow.png" class="img-link"/>
        </div>
      </div>
    <div class="margin-line"></div>
    <div class="hang flex" @click="setgjs()" v-if="myMode == '朋友圈自动评论'">
      <div class="des one-line">设置评论内容</div>
      <div class="tips one-line">{{myGreet}}</div>
      <div class="imgicon">
        <img src="../assets/ic_right_arow.png" class="img-link"/>
      </div>
    </div>
    <div class="margin-line" v-if="myMode == '朋友圈自动评论'"></div>
    <!--<div class="bottom-line"></div>-->
  </div>
   <div class="hang_bottom flex">
     <div class="bottom_hang flex" v-if="needCoin != '0金币'">
       <div class="des1 one-line">{{myCoinDes}}</div>
       <div class="des2 one-line">{{needCoin}}</div>
       <div class="des3 one-line">{{myCoin}}</div>
     </div>
     <div class="bottom_btn" @click="gafjs()">{{btnText}}</div>
   </div>
  </div>
</template>

<script>
export default{
  name: 'PraiseCommit',
  data () {
    return {
      sexSel: '',
      switchSel: '',
      btnText: '',
      groupSel: 'a',
      needCoin: '',
      myCoinDes: '',
      myCoin: '',
      myMode: '',
      myShield: '邀请全部好友',
      myPlace: '',
      myArea: '',
      myGreet: '请设评论内容',
      selNum: '选填'
    }
  },
  mounted () {
    window.setSexSel = this.setSexSel
    window.setSwitchSel = this.setSwitchSel
    window.setBtnText = this.setBtnText
    window.setNeedcoin = this.setNeedcoin
    window.setMycoin = this.setMycoin
    window.setMycoindes = this.setMycoindes
    window.setMymode = this.setMymode
    window.setMyplace = this.setMyplace
    window.setGreet = this.setGreet
    window.setShieldSel = this.setShieldSel
    window.setAreaSel = this.setAreaSel
    window.setGroupSel = this.setGroupSel
    window.setSelNum = this.setSelNum
  },
  methods: {
    setSexSel (sel) {
      this.sexSel = sel
      console.log('type')
      console.log(this.sexSel)
      return sel
    },
    setSwitchSel (sel) {
      this.switchSel = sel
      return sel
    },
    setSelNum (sel) {
      this.selNum = sel
      return sel
    },
    setGroupSel (sel) {
      this.groupSel = sel
      return sel
    },
    setAreaSel (sel) {
      this.myArea = sel
      return sel
    },
    setShieldSel (sel) {
      this.myShield = sel
      return sel
    },
    setGreet (sel) {
      this.myGreet = sel
      return sel
    },
    setBtnText (txt) {
      this.btnText = txt
      return txt
    },
    setNeedcoin (txt) {
      this.needCoin = txt
      return txt
    },
    setMycoin (txt) {
      this.myCoin = txt
      return txt
    },
    setMycoindes (txt) {
      this.myCoinDes = txt
      return txt
    },
    setMymode (txt) {
      this.myMode = txt
      return txt
    },
    setMyplace (txt) {
      this.myPlace = txt
      return txt
    },
    gafjs () {
      if (window.bridge) {
        window.bridge.gaf()
      }
    },
    sexnjs () {
      if (window.bridge) {
        window.bridge.sexn()
      }
    },
    sexmjs () {
      if (window.bridge) {
        window.bridge.sexm()
      }
    },
    sexwjs () {
      if (window.bridge) {
        window.bridge.sexw()
      }
    },
    setpjs () {
      if (window.bridge) {
        window.bridge.setp()
      }
    },
    setgjs () {
      if (window.bridge) {
        window.bridge.setg()
      }
    },
    setareajs () {
      if (window.bridge) {
        window.bridge.setarea()
      }
    },
    setlabeljs () {
      if (window.bridge) {
        window.bridge.setlabel()
      }
    },
    switchonjs () {
      if (window.bridge) {
        window.bridge.switchon()
      }
    },
    switchoffjs () {
      if (window.bridge) {
        window.bridge.switchoff()
      }
    },
    setmjs () {
      if (window.bridge) {
        window.bridge.setm()
      }
    },
    setselnumjs () {
      if (window.bridge) {
        window.bridge.setselnum()
      }
    }
  }
}
</script>

