    bottom:0;
    margin:auto;
    width: 64px;
    height: 25px;
    opacity: 0.6;
    border-radius: 15px;
    border: solid 1px #1a79ff;
  }
  .hang .des_sex_none{
    position: absolute;
    font-size: 14px;
