       <div class="des2 one-line">{{needCoin}}</div>
       <div class="des3 one-line">{{myCoin}}</div>
     </div>
     <div class="bottom_btn" @click="gafjs()">{{btnText}}</div>
   </div>
  </div>
</template>

<script>
export default{
  name: 'ContactAddFriend',
  data () {
    return {
      sexSel: '',
      switchSel: '',
      oneWaySel: '',
      btnText: '',
      needCoin: '',
      myCoin: '',
      myMode: '',
      myPlace: '',
      delMode: '清理所有好友',
      myGreet: '请设置打招呼语',
      myCoinDes: '',
      myShield: '未设置',
      myShiname: '未设置',
      selNum: '选填'
    }
  },
  mounted () {
    window.setSexSel = this.setSexSel
    window.setSwitchSel = this.setSwitchSel
    window.setBtnText = this.setBtnText
    window.setNeedcoin = this.setNeedcoin
    window.setMycoin = this.setMycoin
    window.setMymode = this.setMymode
    window.setMyplace = this.setMyplace
    window.setGreet = this.setGreet
    window.setMycoindes = this.setMycoindes
    window.setShieldSel = this.setShieldSel
    window.setOneWaySel = this.setOneWaySel
    window.setDelMode = this.setDelMode
    window.setShinameSel = this.setShinameSel
    window.setSelNum = this.setSelNum
  },
  methods: {
    setSexSel (sel) {
      this.sexSel = sel
      console.log('type')
      console.log(this.sexSel)
      return sel
    },
    setSwitchSel (sel) {
      this.switchSel = sel
      return sel
    },
    setSelNum (sel) {
      this.selNum = sel
      return sel
    },
    setOneWaySel (sel) {
      this.oneWaySel = sel
      return sel
    },
    setDelMode (sel) {
      this.delMode = sel
      return sel
    },
    setShieldSel (sel) {
      this.myShield = sel
      return sel
    },
    setShinameSel (sel) {
      this.myShiname = sel
      return sel
    },
    setGreet (sel) {
      this.myGreet = sel
      return sel
    },
    setBtnText (txt) {
      this.btnText = txt
      return txt
    },
    setNeedcoin (txt) {
      this.needCoin = txt
      return txt
    },
    setMycoin (txt) {
      this.myCoin = txt
      return txt
    },
    setMycoindes (txt) {
      this.myCoinDes = txt
      return txt
    },
    setMymode (txt) {
      this.myMode = txt
      return txt
    },
    setMyplace (txt) {
      this.myPlace = txt
      return txt
    },
    gafjs () {
      if (window.bridge) {
        window.bridge.gaf()
      }
    },
    sexnjs () {
      if (window.bridge) {
        window.bridge.sexn()
      }
    },
    sexmjs () {
      if (window.bridge) {
        window.bridge.sexm()
      }
    },
    sexwjs () {
      if (window.bridge) {
        window.bridge.sexw()
      }
    },
    setpjs () {
      if (window.bridge) {
        window.bridge.setp()
      }
    },
    setgjs () {
      if (window.bridge) {
        window.bridge.setg()
      }
    },
    switchonjs () {
      if (window.bridge) {
        window.bridge.switchon()
      }
    },
    switchoffjs () {
      if (window.bridge) {
        window.bridge.switchoff()
      }
    },
    setlabeljs () {
      if (window.bridge) {
        window.bridge.setlabel()
      }
    },
    setShienamejs () {
      if (window.bridge) {
        window.bridge.setShiname()
      }
    },
    onewayonjs () {
      if (window.bridge) {
        window.bridge.onewayon()
      }
    },
    delalljs () {
      if (window.bridge) {
        window.bridge.delall()
      }
    },
    delunmarkjs () {
      if (window.bridge) {
        window.bridge.delunmark()
      }
    },
    delunlabeljs () {
      if (window.bridge) {
        window.bridge.delunlabel()
      }
    },
    delspijs () {
      if (window.bridge) {
        window.bridge.delspi()
      }
    },
    setmjs () {
      if (window.bridge) {
        window.bridge.setm()
      }
    },
    setselnumjs () {
      if (window.bridge) {
        window.bridge.setselnum()
      }
    }
  }
}
</script>

